export default {
	data() {
		return {
			userinfo:{},
			orderTypeLise:[
				//name-标题 icon-图标 badge-角标
				{name:'待付款',icon:'icon-daifukuan',badge:1},
				{name:'待发货',icon:'icon-daifahuo',badge:2},
				{name:'待收货',icon:'icon-daishouhuo',badge:6},
				{name:'待评价',icon:'icon-daipingjia',badge:9},
				{name:'售后服务',icon:'icon-shouhou',badge:0}
			],
			severList:[
				[
					{name:'我的预约',icon:'icon-yueyue'},
					{name:'我的收藏',icon:'icon-shoucang'},
					{name:'我的地址',icon:'icon-dizhi',url:'address/address'}
					
				],
				[
					{name:'服务认证',icon:'icon-yueyue'}
					
					
				],
				[
					{name:'在线客服',icon:'icon-kefu',url:''},
					{name:'意见反馈',icon:'icon-yijianfankui',url:''},
					{name:'平台规则',icon:'icon-guize',url:'rule/rule'},
					{name:'关于我们',icon:'icon-guanyuwomen',url:'aboutUs/aboutUs'}
					
				]
			],
		};
	},
	onLoad() {
		//加载
		this.init();
	},
	methods: {
		init() {
			let userInfo = uni.getStorageSync('userInfo')
			userInfo.integral = "101020"
			//用户信息
			this.userInfo = userInfo	
		},
		//用户点击订单类型
		toOrderType(index){
			// uni.showToast({title: this.orderTypeLise[index].name});
			var toUrl = this.orderTypeLise[index].url;
			uni.navigateTo({
				url:toUrl
			})
		},
		//用户点击列表项
		toPage(list_i,li_i){
			//获取跳转的选项
			var toUrl = this.severList[list_i][li_i].url;
			uni.navigateTo({
				url:toUrl
			})
		}
	}
}
